.row-container {
    width: 100%;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
    .row-heading {
        flex: 1;
        max-width: 180px;
        margin-right: 2rem;
       

        h1 {
            font-weight: 800;
            font-size: 18px;
            line-height: 24px;
            letter-spacing: -0.04em;
            color: #fff;
        }
    }

    .row-txt {
        flex: 2;
        max-width: 390px;
        display: flex;

        p {
            font-weight: 500;
            font-size: 14px;
            line-height: 24px;
        }
    }
}