.possibles-container {
  
  .possibility-img {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .possibility-content {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;

    h4 {
      font-weight: 500;
      font-size: 16px;
      line-height: 30px;
      color: #71e5ff;
    }

    h4:last-child {
      color: #ff8a71;
    }

    h1 {
      font-weight: 800;
      font-size: 34px;
      line-height: 45px;
      margin: 1rem 0;
    }

    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 30px;
      color: #81afdd;

      margin-bottom: 2rem;
    }
  }
}

@media screen and (max-width: 950px) {
  .possibles-container {
    flex-direction: column;

    .possibility-img {
      margin: 1rem 0;
      img {
        width: unset;
        height: unset;
      }
    }

    .possibility-content {
      margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 700px) {
  .possibility-image img {
    width: 100%;
    height: 100%;
  }
}
