.hero-container {
  .hero-text {
    align-items: flex-start;
    flex: 1;
  }

  .hero-title {
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    text-align: left;
  }

  button {
    padding: 10px 20px;
    border-radius: 0 5px 5px 0;
    // width: 114px;
  }

  input {
    padding: 10px;
    background-color: #052d56;
    color: #ffffff;
    border-radius: 5px 0px 0px 5px;
    &:focus {
      outline: none;
    }
  }
  .hero-img {
    flex: 1;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
