.cta-container {
  background: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 2rem;
  border-radius: 1rem;
  margin: 4rem;

  .cta-content {
    display: flex;
    flex-direction: column;

    text-align: left;
    color: #000;
    p {
      font-size: 12px;
      line-height: 30px;
      font-weight: 500;
    }
    h3 {
      font-size: 24px;
      line-height: 45px;
      font-weight: 800;
    }
  }

  .cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    margin-left: 2rem;
    button {
      background: #000;
      color: #fff;
      font-weight: 700;
      font-size: 18px;
      line-height: 30px;
      padding: 0.5rem 1rem;
      border-radius: 2rem;

      border: none;
      outline: none;
      cursor: pointer;
      min-width: 150px;
    }
  }
}
