.features-wrapper {
  width: 100%;
//   padding: 4rem 6rem;
margin: 6rem auto;
  .features-heading {
    flex: 1;
    justify-content: flex-start;
    align-items: flex-start;

    text-align: left;
    margin-right: 5rem;

    h1 {
      font-size: 34px;
      line-height: 45px;
      font-weight: 800;
    }

    a {
      font-weight: 500;
      font-size: 16px;
      line-height: 30px;
      color: #ff8a71;
      margin-top: 2rem;
    }
  }

  .features-container {
    flex: 1.5;
    justify-content: flex-start;

h1 {
    font-weight: 800;
font-size: 18px;
line-height: 24px;
letter-spacing: -0.04em;
}

    p {
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      color: #81afdd;
    }
  }
}
