* {
  box-sizing: border-box;
}

body {
  font-family: poppins, sans-serif;
  margin: 0;
  padding: 0;
  background: #040C18;

  color: #ffffff;
  font-family: Manrope;
  // filter: blur(564px);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

button {
  cursor: pointer;
  border: none;
}

input {
  border: none;
}
